import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { Observable } from 'rxjs';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { PedidosService } from 'src/app/providers/Api/Pedidos.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-pedido-info',
  templateUrl: './pedido-info.page.html',
  styleUrls: ['./pedido-info.page.scss'],
})
export class PedidoInfoPage implements OnInit {
  configurando = false;
  cargado = false;
  id = '';

  pedido;

  texto = '';
  estados = [];
  mapa;
  habilitado = false;
  domicilios = [];
  editar = false;
  cargan = false;
  frenar = false;
  constructor(
    private router: Router,
    private alert: AlertController,
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiPedidos: PedidosService,
    private active: ActivatedRoute,
    private socket: Socket
  ) { 

    this.escucharNotificacion().subscribe((data: any) => {
      if (data.action === 'pedido') {
        this.informaciónPedido();
    } 
    })
  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.frenar = false;
    this.informaciónPedido();
  }

  refrescando(event) {
    this.informaciónPedido().then(() => {
        event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }




  async informaciónPedido() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.id = info._id;

        this.apiPedidos.infoPedido(info.token, {id: this.active.snapshot.paramMap.get('id')}).subscribe((data: any) => {
            console.log(data)
            if (data.exe) {
            if (data.response.length > 0) {
              this.pedido = data.response[0];
              this.domicilios = data.response[0].accesos.filter((item) => {
                return item._id !== this.pedido.creador._id;
              })
              this.texto = data.response[0].descripcion;
              this.estadosLista(this.pedido);
              this.estadosMapa(this.pedido);
            }
           
            } else {
              this.toast.toastError('No se pudo consultar el pedido, inténtelo más tarde');
              this.router.navigate(['/central/menu/'])
            }
            this.cargado = true;
        }, (err) => {
              this.toast.toastError('No se pudo consultar el pedido, inténtelo más tarde');
              this.router.navigate(['/central/menu/'])
        })
      }
  }


  async estadosLista(pedido) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiPedidos.consultarEstados(info.token, {pedido}).subscribe((data: any) => {
            if (data.exe) {
              console.log(data)
           this.estados = data.response;
           
            } else {
              this.toast.toastError('No se pudo consultar el pedido, inténtelo más tarde');
            }
        }, (err) => {
              this.toast.toastError('No se pudo consultar el pedido, inténtelo más tarde');
        })
      }
  }



  
  async estadosMapa(pedido) {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        this.apiPedidos.consultarMapa(info.token, {pedido}).subscribe((data: any) => {
            if (data.exe) {
              console.log(data)

              if (data.response.length > 0) {
                this.mapa = data.response[0];
                this.habilitado = this.mapa.apagar;
              }
              
              this.cargan = true;
            } else {
              this.toast.toastError('No se pudo consultar el mapa, inténtelo más tarde');
            }
        }, (err) => {
              this.toast.toastError('No se pudo consultar el mapa, inténtelo más tarde');
        })
      }
  }


  async guardar() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

        // tslint:disable-next-line:max-line-length
        this.apiPedidos.infoPedidoPut(info.token, {id: this.active.snapshot.paramMap.get('id'), texto: this.texto}).subscribe((data: any) => {
            console.log(data)
            if (data.exe) {
            if (data.response) {
              this.texto = data.response.descripcion;
            }
            this.toast.toastExitoso('Descripción actualizada');
            } else {
              this.toast.toastError('No se pudo guardar la descripción del pedido, inténtelo más tarde');
              this.router.navigate(['/central/menu/'])
            }
            this.cargado = true;
        }, (err) => {
              this.toast.toastError('No se pudo guardar la descripción del pedido, inténtelo más tarde');
              this.router.navigate(['/central/menu/'])
        })
      }
  }

  async cambiarEstado( event ) {
    console.log(event)
    const alert = await this.alert.create({
      header: 'Cambiar Estado',
      // tslint:disable-next-line:max-line-length
      message: '¿Estas seguro de cambiar el estado del pedido a:' + event.detail.value,
  
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
           
          }
        },
        {
          text: 'Crear',
          cssClass: 'botonalerta',
          handler: async (data) => {
            const info = await this.almacenamiento.obtenerToken();

            if (info) {
              // tslint:disable-next-line:max-line-length
              this.apiPedidos.crearEstados(info.token, {pedido: this.pedido._id, estado: event.detail.value, destino: this.pedido.creador._id, usuarioID:  this.pedido.creador.usuarioID}).subscribe((res: any) => {
                if (res.exe) {
                  this.estados.push(res.response)
                  this.toast.Especial('El pedido ha cambiado de estado');
                  this.socket.emit('estadosPedido', {accesos: this.pedido.accesos, id: this.pedido._id, estado: event.detail.value});
                } else {
                  this.toast.toastError('No se pudo cambiar el estado, inténtelo más tarde')
                }
              }, (err) => {
                this.toast.toastError('No se pudo cambiar el estado, inténtelo más tarde')
              })
            }
          }
        }
      ]
    })

    await alert.present();
  }

  async habilitar(event) {
    console.log( event.detail.checked);
    this.habilitado = event.detail.checked;
    if (!this.mapa) {
      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {

      this.apiPedidos.crearMapa(info.token, {pedido: this.pedido._id, destino: this.pedido.creador._id}).subscribe((data: any) => {
          if (data.exe) {

              this.mapa = data.response;
              this.habilitado = true;
              this.toast.toastExitoso('Domicilio habilitado');
          } else {
            this.toast.toastError('No se pudo crear el domicilio, inténtelo más tarde');
          }
      }, (err) => {
            this.toast.toastError('No se pudo crear el domicilio, inténtelo más tarde');
      })
    }
    } else {

      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {

        if (this.editar) {

      // tslint:disable-next-line:max-line-length
      this.apiPedidos.editarMapaInfo(info.token, {id: this.mapa._id, destino: this.pedido.creador._id, apagar: event.detail.checked}).subscribe((data: any) => {
          if (data.exe) {

              this.mapa = data.response;
              this.toast.toastExitoso('Domicilio editado');
          } else {
            this.toast.toastError('No se pudo editar el domicilio, inténtelo más tarde');
          }
      }, (err) => {
            this.toast.toastError('No se pudo editar el domicilio, inténtelo más tarde');
      })

    } else {
      this.editar = true;
    }
    }

    }
  }

  async escogerdomiciliario(event) {
    console.log(event)
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {

    // tslint:disable-next-line:max-line-length
    this.apiPedidos.editarMapaInfoD(info.token, {id: this.mapa._id, domiciliario: event.detail.value._id}).subscribe((data: any) => {
        if (data.exe) {

            this.mapa = data.response;
            this.toast.toastExitoso('Domicilio editado');
        } else {
          this.toast.toastError('No se pudo editar el domicilio, inténtelo más tarde');
        }
    }, (err) => {
          this.toast.toastError('No se pudo editar el domicilio, inténtelo más tarde');
    })
  }

}

escucharNotificacion() {
  if (!this.frenar) {
    const myobserver = new Observable(observer => {
      this.socket.on('notificacion', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  }
}


async navegar() {

  const alert = await this.alert.create({
    header: 'Atención!',
    // tslint:disable-next-line:max-line-length
    message: 'No olvide encender su GPS, si lo tiene encendido haga caso omiso al mensaje',

    buttons: [
      {
        text: 'Cancelar',
        cssClass: 'cancelarAlerta',
        role: 'cancel',
        handler: () => {
         
        }
      },
      {
        text: 'Listo',
        cssClass: 'botonalerta',
        handler: async (data) => {
          if (this.mapa.ubicacion) {

            if (this.mapa.ubicacionactual) {
              this.router.navigate(['/central/pedidos/mapa', 'vivo', this.mapa._id])
            } else {
              this.toast.toastError('Aún no se ha confirmado la ubicación del domiciliario');
            }
           
          } else {
            this.router.navigate(['/central/pedidos/mapa', 'destino', this.mapa._id])
          }
        }
      }
    ]
  })

  await alert.present();
 
}

async entregar() {
  if (this.mapa.ubicacionactual) {
    this.router.navigate(['/central/pedidos/mapa', 'entrega', this.mapa._id])
  } else {
    const alert = await this.alert.create({
      header: 'Atención!',
      message: '¿Ya vas a despachar el pedido?, una vez que aceptes el cliente recibirá una notificación de que su pedido esta en camino, no olvides encender tu GPS ',
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel'
        },
        {
          text: 'Confirmar',
          cssClass: 'botonalerta',
          handler: () => {
            this.socket.emit('entregaPedido', {
              pedido: this.pedido
            })
            this.router.navigate(['/central/pedidos/mapa', 'entrega', this.mapa._id])
          }
        }
      ]
    })
  
    await alert.present();
  }
}


async entregar2() {
  if (this.mapa.ubicacionactual) {
    this.router.navigate(['/central/pedidos/mapa', 'vivo', this.mapa._id])
  } 
}

ionViewWillLeave() {
  this.frenar = true;
}
}
